.svg-map {
    width: 100%;
    height: auto;
    stroke: #000;
    stroke-width: 1;
    stroke-linecap: round;
    stroke-linejoin: round; }
.svg-map__location {
    fill: #fff;
    cursor: pointer; }
.svg-map__location:focus, .svg-map__location:hover {
    fill: #0096DB;
    outline: 0; }
.svg-map__location[aria-checked=true] {
    fill: #f4bc44; }

